<template>
  <div class="index">
    <bread-nav :nav="nav"></bread-nav>
    <main-part v-loading="newLoading">
      <div class="btn-block">
        <div class="btn-main mr15" @click="dialog=true">导入</div>
        <div class="btn-main-deepen mr15"
             @click="$router.push(`/student-work/appraisal/early-selfStudy/entry?tabPick=`+tabPick+``)">录入检查结果</div>
      </div>
      <div class="search-box">
        <div class="item">
          <span class="text">日期</span>
          <el-date-picker
                  v-model="form.inspectDate"
                  type="date"
                  :clearable="false"
                  value-format="yyyy-MM-dd HH:mm:ss"
                  placeholder="选择日期">
          </el-date-picker>
        </div>
      </div>
      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="12.5%">年级</th>
          <th width="12.5%">年级组长缺席</th>
          <th width="12.5%">年级组长迟到</th>
          <th width="12.5%">班主任缺席</th>
          <th width="12.5%">班主任迟到</th>
          <th width="12.5%">班主任助理就位</th>
          <th width="12.5%">检查人</th>
          <th width="12.5%">操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item, key) in anExaminationList">
          <td>{{item.gradeName}}</td>
          <td>
            <span v-for="absentItem in item.gradeLeaderAbsent">{{absentItem.name}} </span>
          </td>
          <td>
            <span v-for="lateItem in item.gradeLeaderLate">{{lateItem.name}} </span>
          </td>
          <td>
            <span v-for="headTeacherAbsentItem in item.headmasterAbsent">{{headTeacherAbsentItem.name}} </span>
          </td>
          <td>
            <span v-for="headTeacherLateItem in item.headmasterLate">{{headTeacherLateItem.name}} </span>
          </td>
          <td>
            <span v-for="headTeacherInPlaceItem in item.headmasterAssistantInPlace">{{headTeacherInPlaceItem.name}} </span>
          </td>
          <td>{{item.inspectUser}}</td>
          <td>
            <span class="oper"
                  @click="$router.push(`/student-work/appraisal/early-selfStudy/entry?tabPick=`+tabPick+`&gradeId=`+item.gradeId+`&inspectDate=`+item.inspectDate)">编辑</span>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!anExaminationList.length"></no-data>


      <!--导入-->
      <el-dialog title="导入" class="dialog" :visible.sync="dialog" width="540px">
        <div class="item">
          <span class="title imp">日期</span>
          <el-date-picker
                  type="date"
                  v-model="inspectDate"
                  placeholder="选择日期" style="width: 86%">
          </el-date-picker>
        </div>
        <import-excel @change="change" :type="$enums.InspectType.getName(this.tabPick)+'导入模板' " />
        <div class="dialog_btns">
          <div @click="impFun" class="btn-submit btn-upload mr15">确认</div>
          <div @click="dialog = false" class="btn-clear">返回</div>
        </div>
      </el-dialog>
    </main-part>
  </div>
</template>
<script>
  export default {
    data () {
      let vm = this
      return {
        tabPick: vm.$route.query.tabPick,
        nav: [{name: null, path: 'go(-1)'}, {name: null, path: ''}],
        form: {
          inspectDate: new Date(new Date(new Date().getTime() - 3600 * 1000 * 24)).$Format('yyyy-MM-dd' + ' 00:00:00'),
        },
        anExaminationList: [],
        newLoading: false,

        // 导入
        dialog: false,
        files: null,
        inspectDate: null,
      }
    },
    watch: {
      'form.inspectDate' () {
        this.getAnExaminationList()
      }
    },
    methods: {
      // 处理面包屑
      getName: function (val) {
        switch (val) {
          case 'MORNING_LESSON':
            return '查询早自习到岗检查记录'
          case 'BREAK_EXERCISE':
            return '查询课间操跟跑检查记录'
          case 'EVENING_LESSON':
            return '查询晚自习到岗检查记录'
          case 'REGULAR_MEETING':
            return '查询班主任例会检查记录'
          default:
            return val
        }
      },
      // 请求列表
      async getAnExaminationList () {
        this.anExaminationList = await this.$ajax({
          url: '/assess/queryAssessInspectRecordList.do',
          el: '.table',
          query: this.form
        })
      },
      change (e) {
        this.files = e.target.files[0]
      },
      // 导入
      async impFun () {
        if (!this.inspectDate) {
          this.$message.warning('请选择日期')
          return
        }
        let file = this.files
        if (!file) {
          this.$message.warning('请选择上传文件')
          return
        }
        let formData = new FormData()
        formData.append('file', file)
        formData.append('inspectDate', this.inspectDate)
        formData.append('inspectType', this.tabPick)
        // formData.append('courseId', this.form.weekId)
        let obj = {
          url: '/import/importAssessInspectRecord.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        if (res.errMsgList && res.errMsgList.length) {
          this.$message.error(res.errMsgList[0])
        } else {
          // this.$refs.upload.value = null
          this.$remind('导入成功')
          this.dialog = false
          this.$search(this.table)
        }
      },
    },
    async mounted () {
      this.newLoading = true
      this.nav[0].name = this.$enums.InspectType.getName(this.tabPick)
      this.nav[1].name = this.getName(this.tabPick)
      this.form.inspectType = this.tabPick
      await this.getAnExaminationList()
      this.newLoading = false
    }
  }
</script>
<style lang="sass" scoped>
    .btns
      position: absolute
      right: 15px
      top: 12px
      border: 0

    .search-box
      margin-top: 15px
      padding-bottom: 20px
      .item
        display: inline-block
        margin-right: 15px
      .text
        margin-left: 5px
        color: $main-c
        display: block
        margin-bottom: 3px

    .dialog
      .title
        width: 70px
        line-height: 40px
        vertical-align: top
        display: inline-block
      .input
        width: calc(100% - 110px)
      .imp:before
        content: '*'
        color: $error-c
        margin-right: 4px
      .file-input
        /deep/ input
          padding-top: 10px
          line-height: 1
      .item
        margin-bottom: 20px
      .dialog_btns
        margin-top: 15px
        text-align: center
</style>
